<template>
  <div class="bigbox">
    <el-menu style="" class="leftNav" unique-opened>
      <el-submenu
        :index="index + ''"
        v-for="(item, index) in leftNavData"
        :key="index"
      >
        <template slot="title">{{ index }}</template>
        <el-submenu
          :index="index2 + ''"
          v-for="(item2, index2) in item"
          :key="index2"
        >
          <template slot="title">{{ index2 }}</template>
          <el-menu-item
            :index="index2 + item3"
            v-for="(item3, index3) in item2"
            :key="index3"
            @click="NavClick(index, index2, item3)"
            >{{ item3 }}</el-menu-item
          >
        </el-submenu>
      </el-submenu>
    </el-menu>
    <div style="margin-left: 13.5vw">
      <h3 style="color: white; margin-bottom: 1vh">客户数据库</h3>
      <el-button type="primary" @click="openAdd()">新 增</el-button>
      <el-button type="primary" @click="openEditDialog()">编 辑</el-button>
      <table class="customerDatabaseTable">
        <thead>
          <tr>
            <th></th>
            <th>序号</th>
            <th>渠道</th>
            <th>站点</th>
            <th>国家</th>
            <th>客户名</th>
            <th>收件地址</th>
            <th>电话</th>
            <th>邮编</th>
            <th>客户邮箱</th>
            <th>客户评级</th>
            <th>购买偏好</th>
          </tr>
        </thead>
        <tbody>
          <tr
            v-for="(item, index) in tableData"
            :key="index"
            @click="showClientData(item.id)"
            style="cursor: pointer"
          >
            <td>
              <input
                type="checkbox"
                name=""
                id=""
                class="checkLi"
                :value="item.id"
                @click="checkClick(index)"
              />
            </td>
            <td>{{ index + 1 + (page - 1) * 50 }}</td>
            <td>{{ item.platform }}</td>
            <td>{{ item.site }}</td>
            <td>{{ item.country }}</td>
            <td>{{ item.customer_name }}</td>
            <td>{{ item.shipping_address }}</td>
            <td>{{ item.phone }}</td>
            <td>{{ item.postcode }}</td>
            <td>{{ item.mail }}</td>
            <td>{{ item.statue }}</td>
            <td>{{ item.likes }}</td>
          </tr>
        </tbody>
      </table>
      <el-pagination
        background
        @current-change="handleCurrentChange"
        :current-page="page"
        layout="prev, pager, next, jumper"
        :total="total"
        class="pagetotle"
        style="margin: 1vh 0 0 30vw"
      >
      </el-pagination>
      <div class="bottomBox">
        <h4>客户详细信息</h4>
        <div>
          渠道：
          <el-input
            v-model="detailChannel"
            placeholder=""
            style="width: 10vw"
            readonly=""
          ></el-input>
          国家：<el-input
            v-model="detailCountry"
            placeholder=""
            style="width: 10vw"
            readonly=""
          ></el-input>
        </div>
        <div>
          站点：<el-input
            v-model="detailSite"
            placeholder=""
            style="width: 10vw"
            readonly=""
          ></el-input>
          客户名：<el-input
            v-model="detailName"
            detailName=""
            style="width: 11vw"
            readonly=""
          ></el-input>
        </div>
        <div>
          邮箱：<el-input
            v-model="detailemil"
            placeholder=""
            style="width: 11vw"
            readonly=""
          ></el-input>
          电话：<el-input
            v-model="detailPhone"
            placeholder=""
            style="width: 11vw"
            readonly=""
          ></el-input>
        </div>
        <div>
          邮编：<el-input
            v-model="detailemailDo"
            placeholder=""
            style="width: 11vw"
            readonly=""
          ></el-input>
        </div>
        <div>
          购买偏好：<el-input
            v-model="detailHobby"
            placeholder=""
            style="width: 11vw"
            readonly=""
          ></el-input>
          客户评级：<el-input
            v-model="detailBlackAndWhite"
            placeholder=""
            style="width: 11vw"
            readonly=""
          ></el-input>
        </div>
        <div>
          收件地址：
          <el-input
            type="textarea"
            :rows="4"
            placeholder=""
            v-model="detailAddress"
            style="width: 30vw; resize: none"
            readonly=""
          >
          </el-input>
        </div>
        <div>
          过往购买记录：
          <table>
            <thead>
              <tr>
                <th>序号</th>
                <th>订单编号</th>
                <th>sku</th>
                <th>购买数量</th>
              </tr>
            </thead>
            <tbody>
              <tr>
                <td>1</td>
                <td>{{ buyRecord[0] }}</td>
                <td>{{ buyRecord[1] }}</td>
                <td>{{ buyRecord[2] }}</td>
              </tr>
            </tbody>
          </table>
        </div>
        <div>
          测评&surprise记录：
          <table>
            <thead>
              <tr>
                <th>序号</th>
                <th>时间</th>
                <th>sku</th>
                <th>宣发类型</th>
              </tr>
            </thead>
            <tbody>
              <tr>
                <td></td>
                <td></td>
                <td></td>
                <td></td>
              </tr>
            </tbody>
          </table>
        </div>
        <div>
          <el-button type="danger" @click="closeMain()">关 闭</el-button>
        </div>
      </div>
    </div>
    <el-dialog
      title="新增"
      :visible.sync="centerDialogVisible"
      :close-on-click-modal="false"
      width="30%"
      center
    >
      <span class="dialogSpan">
        <div>
          渠道：<el-select
            style="width: 10vw"
            v-model="addDitch"
            placeholder="请选择"
          >
            <el-option
              v-for="item in addDitchList"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>
          国家：<el-select
            v-model="addCountry"
            placeholder="请选择"
            style="width: 10vw"
          >
            <el-option
              v-for="item in addCountryList"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>
        </div>
        <div>
          站点：<el-select
            v-model="addSite"
            placeholder="请选择"
            style="width: 10vw"
          >
            <el-option
              v-for="item in addSiteList"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>
          客户名：<el-input
            v-model="addcustomerName"
            placeholder=""
            style="width: 9.2vw"
          ></el-input>
        </div>
        <div>
          邮箱：<el-input
            v-model="addMail"
            placeholder=""
            style="width: 10vw"
          ></el-input
          >电话：<el-input
            v-model="addPhone"
            placeholder=""
            style="width: 10vw"
          ></el-input>
        </div>
        <div>
          邮编：<el-input
            v-model="addPostcode"
            placeholder=""
            style="width: 10vw"
          ></el-input>
        </div>
        <div>
          购买偏好：<el-select
            v-model="addLikes"
            placeholder="请选择"
            style="width: 9vw"
          >
            <el-option
              v-for="item in addLikesList"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>

          客户评级：<el-select
            v-model="addGoodAndBad"
            placeholder="请选择"
            style="width: 9vw"
          >
            <el-option
              v-for="item in addGoodAndBadList"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>
        </div>
        <div>
          收件地址：<el-input
            v-model="addAddress"
            placeholder=""
            style="width: 22vw"
          ></el-input>
        </div>
      </span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="centerDialogVisible = false">取 消</el-button>
        <el-button type="warning" @click="addReset()">重 置</el-button>
        <el-button type="primary" @click="addNewClient()">确 定</el-button>
      </span>
    </el-dialog>

    <el-dialog
      title="编辑"
      :visible.sync="centerDialogVisible2"
      :close-on-click-modal="false"
      width="30%"
      center
    >
      <span class="dialogSpan">
        <div>
          渠道：<el-select
            style="width: 10vw"
            v-model="editDitch"
            placeholder="请选择"
          >
            <el-option
              v-for="item in editDitchList"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>
          国家：<el-select
            v-model="editCountry"
            @change="changeEditCountry"
            placeholder="请选择"
            style="width: 10vw"
          >
            <el-option
              v-for="item in editCountryList"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>
        </div>
        <div>
          站点：<el-select
            v-model="editSite"
            placeholder="请选择"
            style="width: 10vw"
          >
            <el-option
              v-for="item in editSiteList"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>
          客户名：<el-input
            v-model="editcustomerName"
            placeholder=""
            style="width: 9.2vw"
          ></el-input>
        </div>
        <div>
          邮箱：<el-input
            v-model="editMail"
            placeholder=""
            style="width: 10vw"
          ></el-input
          >电话：<el-input
            v-model="editPhone"
            placeholder=""
            style="width: 10vw"
          ></el-input>
        </div>
        <div>
          邮编：<el-input
            v-model="editPostcode"
            placeholder=""
            style="width: 10vw"
          ></el-input>
        </div>
        <div>
          购买偏好：<el-select
            v-model="editLikes"
            placeholder="请选择"
            style="width: 9vw"
          >
            <el-option
              v-for="item in editLikesList"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>

          客户评级：<el-select
            v-model="editGoodAndBad"
            placeholder="请选择"
            style="width: 9vw"
          >
            <el-option
              v-for="item in editGoodAndBadList"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>
        </div>
        <div>
          收件地址：<el-input
            v-model="editAddress"
            placeholder="请输入内容"
            style="width: 22vw"
          ></el-input>
        </div>
      </span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="centerDialogVisible2 = false">取 消</el-button>
        <el-button type="danger" @click="editClientdata()">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      detailChannel: "", //详细信息渠道
      detailSite: "", //详细信息站点
      detailCountry: "", //详细信息国家
      detailName: "", //详细信息客户名
      detailemil: "", //详细信息邮箱
      detailPhone: "", //详细信息电话
      detailemailDo: "", //邮编
      detailHobby: "", //购买偏好
      detailBlackAndWhite: "", //客户黑白名单
      detailAddress: "", //详细信息收件地址
      tableData: [], //表格数据
      leftNavData: [], //左边导航栏
      firstNav: "", //一级导航
      twiceNav: "", //二级导航
      threeNav: "", //三级导航
      total: 0, //页码当前页
      page: 1, //
      buyRecord: [], //过往购买记录

      centerDialogVisible: false, //弹框用
      centerDialogVisible2: false, //弹框用

      addDitch: "", //新增渠道
      addDitchList: [], //新增渠道选择器
      addSite: "", //新增站点
      addSiteList: [], //新增站点选择器
      addCountry: "", //新增国家
      addCountryList: [], //新增国家选择器
      addcustomerName: "", //新增客户名
      addMail: "", //新增邮箱
      addPostcode: "", //新增邮编
      addPhone: "", //新增电话
      addLikes: "", //新增购买偏好
      addLikesList: [
        {
          value: "所有",
          label: "所有"
        },
        {
          value: "钢木",
          label: "钢木"
        },
        {
          value: "膜片",
          label: "膜片"
        }
      ], //购买喜好下拉框
      addAddress: "", //新增收件地址
      addGoodAndBad: "", //客户评级
      addGoodAndBadList: [
        {
          value: "白名单",
          label: "白名单"
        },
        {
          value: "黑名单",
          label: "黑名单"
        } //客户评级下拉框
      ], //客户评级下拉框

      editDitch: "", //编辑渠道
      editDitchList: [], //编辑渠道选择器
      editSite: "", //编辑站点
      editSiteList: [], //编辑站点选择器
      editCountry: "", //编辑国家
      editCountryList: [], //编辑国家选择器
      editcustomerName: "", //编辑客户名
      editMail: "", //编辑邮箱
      editPostcode: "", //编辑邮编
      editPhone: "", //编辑电话
      editLikes: "", //编辑购买偏好
      editLikesList: [
        {
          value: "所有",
          label: "所有"
        },
        {
          value: "钢木",
          label: "钢木"
        },
        {
          value: "膜片",
          label: "膜片"
        }
      ], //购买喜好下拉框
      editAddress: "", //编辑收件地址
      editGoodAndBad: "", //客户评级
      editGoodAndBadList: [
        {
          value: "白名单",
          label: "白名单"
        },
        {
          value: "黑名单",
          label: "黑名单"
        } //客户评级下拉框
      ]
    };
  },
  methods: {
    getDefaultData() {
      //获取左边导航栏和表格数据
      axios({
        url: this._url2 + "server/customer_management/customer_database",
        method: "get",
        params: {
          page: 1
        }
      }).then(res => {
        console.log(res);
        this.tableData = res.data.data_all;
        // this.leftNavData = res.data.data_dict;
        this.total = res.data.count_num[0].count_num / 5;
        for (let key in res.data.data_dict) {
          let obj = {
            label: key,
            value: key
          };
          this.addDitchList.push(obj);
          this.editDitchList.push(obj);
        }
      });
    },
    NavClick(index, index2, item3) {
      //点击左边导航栏事件
      this.firstNav = index;
      this.twiceNav = index2;
      this.threeNav = item3;
      axios({
        url: this._url2 + "server/customer_management/customer_database",
        method: "get",
        params: {
          platform: this.firstNav,
          country: this.twiceNav,
          site: this.threeNav,
          page: 1
        }
      }).then(res => {
        console.log(res);
        this.tableData = res.data.data_all;
        this.$nextTick(() => {
          $(".customerDatabaseTable>tbody").scrollTop(0);
        });
        this.total = res.data.count_num[0].count_num / 5;
        this.page = 1;
      });
    },
    handleCurrentChange(newPage) {
      // 页码切换会触发此函数
      this.page = newPage;
      if (this.firstNav == "") {
        axios({
          url: this._url2 + "server/customer_management/customer_database",
          method: "get",
          params: {
            page: this.page
          }
        }).then(res => {
          console.log(res);
          this.tableData = res.data.data_all;
          this.$nextTick(() => {
            $(".customerDatabaseTable>tbody").scrollTop(0);
          });
        });
      } else {
        axios({
          url: this._url2 + "server/customer_management/customer_database",
          method: "get",
          params: {
            platform: this.firstNav,
            country: this.twiceNav,
            language: this.threeNav,
            page: this.page
          }
        }).then(res => {
          console.log(res);
          this.tableData = res.data.data_all;
          this.$nextTick(() => {
            $(".customerDatabaseTable>tbody").scrollTop(0);
          });
          let checkLi = document.querySelectorAll(".checkLi");
          for (let i = 0; i < checkLi; i++) {
            checkLi[i].checked = false;
          }
        });
      }
    },
    openAdd() {
      //打开新增界面
      this.centerDialogVisible = true;
      this.addDitch = "";
      this.addSite = "";
      this.addCountry = "";
      this.addcustomerName = "";
      this.addMail = "";
      this.addPostcode = "";
      this.addPhone = "";
      this.addLikes = "";
      this.addAddress = "";
      this.addGoodAndBad = "";
    },
    addReset() {
      //新增界面重置
      this.addDitch = "";
      this.addSite = "";
      this.addCountry = "";
      this.addcustomerName = "";
      this.addMail = "";
      this.addPostcode = "";
      this.addPhone = "";
      this.addLikes = "";
      this.addAddress = "";
      this.addGoodAndBad = "";
    },
    addNewClient() {
      //新增一条客户
      if (
        this.addDitch != "" &&
        this.addSite != "" &&
        this.addCountry != "" &&
        this.addcustomerName != "" &&
        this.addAddress != "" &&
        this.addPhone != "" &&
        this.addPostcode != "" &&
        this.addMail != "" &&
        this.addGoodAndBad != "" &&
        this.addLikes != ""
      ) {
        let formData = new FormData();
        formData.append("platform", this.addDitch);
        formData.append("site", this.addSite);
        formData.append("country", this.addCountry);
        formData.append("customer_name", this.addcustomerName);
        formData.append("shipping_address", this.addAddress);
        formData.append("phone", this.addPhone);
        formData.append("postcode", this.addPostcode);
        formData.append("mail", this.addMail);
        formData.append("statue", this.addGoodAndBad);
        formData.append("likes", this.addLikes);
        this.$axios
          .post(
            this._url2 + "server/customer_management/customer_database",
            formData
          )
          .then(res => {
            if (res.data.code == 200) {
              alert("新增成功");
              this.centerDialogVisible = false;
            }
            if (this.firstNav == "") {
              axios({
                url:
                  this._url2 + "server/customer_management/customer_database",
                method: "get",
                params: {
                  page: this.page
                }
              }).then(res => {
                console.log(res);
                this.tableData = res.data.data_all;
              });
            } else {
              axios({
                url:
                  this._url2 + "server/customer_management/customer_database",
                method: "get",
                params: {
                  platform: this.firstNav,
                  country: this.twiceNav,
                  language: this.threeNav,
                  page: this.page
                }
              }).then(res => {
                console.log(res);
                this.tableData = res.data.data_all;
              });
            }
          });
      } else {
        alert("所有字段都不能为空！");
      }
    },
    openEditDialog() {
      //打开编辑框判断
      let checkLi = document.querySelectorAll(".checkLi");
      let id = "";
      for (let i = 0; i < checkLi.length; i++) {
        if (checkLi[i].checked == true) {
          id = checkLi[i].value;
        }
      }
      if (id) {
        this.centerDialogVisible2 = true;
      } else {
        alert("请选择一条数据编辑");
      }
      for (let i = 0; i < this.tableData.length; i++) {
        console.log("复制");
        if (id == this.tableData[i].id) {
          this.editDitch = this.tableData[i].platform;

          this.editSite = this.tableData[i].site;
          console.log(this.tableData[i].site, this.editSite);
          this.editCountry = this.tableData[i].country;
          // 获取列表
          this.getSiteList();
          this.editcustomerName = this.tableData[i].customer_name;
          this.editMail = this.tableData[i].mail;
          this.editPostcode = this.tableData[i].postcode;
          this.editPhone = this.tableData[i].phone;
          this.editLikes = this.tableData[i].likes;
          this.editGoodAndBad = this.tableData[i].statue;

          this.editAddress = this.tableData[i].shipping_address;
        }
      }
    },
    changeEditCountry() {
      this.editSite = "";
      this.getSiteList();
    },
    getSiteList() {
      axios({
        url: this._url2 + "server/personnel_management/get_sign_area/",
        method: "get",
        params: {
          country: this.editCountry
        }
      }).then(res => {
        this.editSiteList = [];
        let demo = res.data.data;
        for (let i = 0; i < demo.length; i++) {
          let obj = {
            value: demo[i],
            label: demo[i]
          };
          this.editSiteList.push(obj);
        }
      });
    },
    editClientdata() {
      //编辑某一条数据
      if (
        this.editDitch != "" &&
        this.editSite != "" &&
        this.editCountry != "" &&
        this.editcustomerName != "" &&
        this.editAddress != "" &&
        this.editPhone != "" &&
        this.editPostcode != "" &&
        this.editMail != "" &&
        this.editGoodAndBad != "" &&
        this.editLikes != ""
      ) {
        let checkLi = document.querySelectorAll(".checkLi");
        let id = "";
        for (let i = 0; i < checkLi.length; i++) {
          if (checkLi[i].checked == true) {
            id = checkLi[i].value;
          }
        }
        console.log(id);
        this.centerDialogVisible2 = false;
        let formData = new FormData();
        formData.append("id", id);
        formData.append("platform", this.editDitch);
        formData.append("site", this.editSite);
        formData.append("country", this.editCountry);
        formData.append("customer_name", this.editcustomerName);
        formData.append("shipping_address", this.editAddress);
        formData.append("phone", this.editPhone);
        formData.append("postcode", this.editPostcode);
        formData.append("mail", this.editMail);
        formData.append("statue", this.editGoodAndBad);
        formData.append("likes", this.editLikes);
        this.$axios
          .put(
            this._url2 + "server/customer_management/customer_database",
            formData
          )
          .then(res => {
            if (res.data.code == 200) {
              alert("编辑成功");
              if (this.firstNav == "") {
                axios({
                  url:
                    this._url2 + "server/customer_management/customer_database",
                  method: "get",
                  params: {
                    page: this.page
                  }
                }).then(res => {
                  console.log(res);
                  this.tableData = res.data.data_all;
                });
              } else {
                axios({
                  url:
                    this._url2 + "server/customer_management/customer_database",
                  method: "get",
                  params: {
                    platform: this.firstNav,
                    country: this.twiceNav,
                    language: this.threeNav,
                    page: this.page
                  }
                }).then(res => {
                  console.log(res);
                  this.tableData = res.data.data_all;
                });
              }
            }
          });
      } else {
        alert("所有字段都不能为空！");
      }
    },
    checkClick(i) {
      //复选框唯一
      let checkLi = document.querySelectorAll(".checkLi");
      for (let i = 0; i < checkLi.length; i++) {
        checkLi[i].checked = false;
      }
      checkLi[i].checked = true;
    },
    closeMain() {
      //底下取消按钮
      document.querySelector(".bottomBox").style.display = "none";
    },
    showClientData(id) {
      //亮出底下的图层并赋值
      document.querySelector(".bottomBox").style.display = "block";
      let checkLi = document.querySelectorAll(".checkLi");
      let demo1 = [];
      let demo2 = [];
      let demo3 = [];
      for (let i = 0; i < this.tableData.length; i++) {
        if (id == this.tableData[i].id) {
          this.detailChannel = this.tableData[i].platform;
          this.detailSite = this.tableData[i].site;
          this.detailCountry = this.tableData[i].country;
          this.detailName = this.tableData[i].customer_name;
          this.detailemil = this.tableData[i].mail;
          this.detailPhone = this.tableData[i].phone;
          this.detailemailDo = this.tableData[i].postcode;
          this.detailHobby = this.tableData[i].likes;
          this.detailAddress = this.tableData[i].shipping_address;
          this.detailBlackAndWhite = this.tableData[i].statue;
          this.buyRecord = [];
          //  for(let j = 0; j < this.tableData[i].order_number.length;j++){
          //    demo1.push(this.tableData[i].order_number[j])
          //  }
          //  let obj1 = {
          //      order_num:demo1
          //   }
          //  for(let j = 0; j < this.tableData[i].sku.length;j++){
          //    demo2.push(this.tableData[i].sku[j])
          //  }
          //  let obj2 = {
          //    sku:demo2
          //  }
          //  for(let j = 0; j < this.tableData[i].quantity.length;j++){
          //    demo3.push(this.tableData[i].quantity[j])
          //  }
          //  let obj3 = {
          //    quantity:demo3
          //  }
          //  this.buyRecord.push(obj1)
          //  this.buyRecord.push(obj2)
          //  this.buyRecord.push(obj3)
          //  console.log(this.buyRecord)   等优化

          this.buyRecord.push(this.tableData[i].order_number);
          this.buyRecord.push(this.tableData[i].sku);
          this.buyRecord.push(this.tableData[i].quantity);
          console.log(this.buyRecord);
        }
      }
    }
  },
  created() {
    this.getDefaultData();
    axios({
      //获取编辑和新增国家下拉框（权限用）
      url: this._url2 + "server/personnel_management/get_sign_country/",
      method: "get"
    }).then(res => {
      let demo = res.data.data;
      for (let i = 0; i < demo.length; i++) {
        let obj = {
          label: demo[i],
          value: demo[i]
        };
        this.addCountryList.push(obj);
        this.editCountryList.push(obj);
      }
    });
    axios({
      //左侧导航栏（权限用）
      url: this._url2 + "server/personnel_management/area_sign2/",
      method: "get"
    }).then(res => {
      console.log(res, 5);
      this.leftNavData = res.data.data;
    });
  },
  mounted() {
    window.addEventListener("scroll", this.handleScroll);
  },
  watch: {
    //下拉框联动代码
    addCountry: function() {
      this.addSite = "";
      axios({
        url: this._url2 + "server/personnel_management/get_sign_area/",
        method: "get",
        params: {
          country: this.addCountry
        }
      }).then(res => {
        this.addSiteList = [];
        let demo = res.data.data;
        for (let i = 0; i < demo.length; i++) {
          let obj = {
            value: demo[i],
            label: demo[i]
          };
          this.addSiteList.push(obj);
        }
      });
    }
