<style scoped>
.leftNav {
  width: 12vw;
  height: 100vh;
  position: fixed;
  left: 0;
}

table {
  /*width: 84vw;*/
  margin: 10px 0px;
  border-collapse: collapse;
}

tbody {
  display: block;
  max-height: 68vh;
  width: fit-content;
  overflow-y: auto;
  border-bottom: 1px #e5e5e5 solid;
}

thead,
tbody tr {
  display: table;
  width: fit-content;
  table-layout: fixed;
}

/*th和td的宽度使用vw或者px*/
thead th,
tbody tr td {
  border: 1px solid #e5e5e5;
  border-top: none;
  text-align: center;
  width: 6vw;
}

table thead tr {
  border-top: 1px solid #e5e5e5;
  background-color: #f7f5f5;
}

table > tbody > tr:hover {
  background: #eaf7e0;
}

table > thead > tr > th {
  font-size: 0.9rem;
  color: #000;
  font-weight: 600;
  height: 2.5vh;
  line-height: 2.5vh;
}

table > tbody > tr > td {
  font-size: 0.7rem;
  color: #000;
  height: 2.5vh;
  line-height: 2.5vh;
}

/* 设置滚动条的大小 */
::-webkit-scrollbar {
  width: 9px;
  height: 9px;
}

::-webkit-scrollbar-thumb {
  border-radius: 5px;
  background-color: #dddddd;
  background-clip: padding-box;
  min-height: 5vh;
}

::-webkit-scrollbar-thumb:hover {
  background-color: #bbb;
}

.bottomBox {
  display: none;
  margin: 2vh 0 5vh 0vw;
  padding: 1vh;
  border: 2px solid #dddddd;
  width: 50vw;
  border-radius: 10px;
  background: rgba(255, 255, 255, 0.9);
}

.bottomBox div {
  margin-top: 2vh;
}

.dialogSpan div {
  margin-bottom: 0.5vh;
}

table tr th:nth-child(1),
table tr td:nth-child(1),
table tr th:nth-child(2),
table tr td:nth-child(2),
table tr th:nth-child(4),
table tr td:nth-child(4),
table tr th:nth-child(5),
table tr td:nth-child(5) {
  width: 3vw;
}

table tr th:nth-child(7),
table tr td:nth-child(7),
table tr th:nth-child(10),
table tr td:nth-child(10) {
  width: 18.6vw;
}

.bigbox >>> .el-textarea .el-textarea__inner {
  resize: none;
}
</style>



